import React from "react"
import ReactDOM from 'react-dom';
import {
  BrowserRouter as Router
} from "react-router-dom";

import App from './App';
import './bootstrap.css'

ReactDOM.render(
  <React.StrictMode>
    <Router basename="/Portfolio">
        <App />
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);